* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Segoe UI", sans-serif;
  background: #000;
  overflow: hidden;
  height: 100vh;
}

.app {
  height: 100vh;
  width: 100vw;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

/* THEME SYSTEM */
.green-theme {
  background: linear-gradient(135deg, #1A3C27, #2E8B57);
}

.purple-theme {
  background: linear-gradient(135deg, #2D1B69, #6A0DAD);
}

.neon-theme {
  background: linear-gradient(135deg, #0A0A0A, #1A3C27);
}

/* PLAYER */
.player {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 25px;
  width: 360px;
  padding: 20px;
  box-shadow: 
    0 15px 35px rgba(0, 0, 0, 0.5),
    inset 2px 2px 10px rgba(255, 255, 255, 0.8),
    inset -2px -2px 10px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  border: 6px solid;
  transition: border-color 0.3s ease;
}

/* THEME БОЙЫНША BORDER ТҮСТЕРІ */
.green-theme .player {
  border-color: #90EE90;
}

.purple-theme .player {
  border-color: #8A2BE2;
  box-shadow: 
    0 15px 35px rgba(0, 0, 0, 0.8),
    inset 2px 2px 10px rgba(255, 255, 255, 0.9),
    inset -2px -2px 10px rgba(0, 0, 0, 0.2),
    0 0 20px rgba(138, 43, 226, 0.4);
}

.neon-theme .player {
  border-color: #39FF14;
  box-shadow: 
    0 15px 35px rgba(0, 0, 0, 0.8),
    inset 2px 2px 10px rgba(255, 255, 255, 0.9),
    inset -2px -2px 10px rgba(0, 0, 0, 0.2),
    0 0 20px rgba(57, 255, 20, 0.4);
}

/* THEME BUTTONS */
.theme-buttons {
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 100;
  display: flex;
  gap: 6px;
}

.theme-buttons button {
  border: none;
  padding: 6px 12px;
  border-radius: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: bold;
  font-size: 11px;
  border: 2px solid #333;
  background: rgba(255, 255, 255, 0.95);
  color: #333;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.theme-buttons button:hover {
  transform: scale(1.05);
  background: rgba(255, 255, 255, 1);
}

.theme-buttons button:active {
  transform: scale(0.95);
}

/* HEADER */
.header {
  text-align: center;
  margin-bottom: 20px;
  width: 100%;
  margin-top: 10px;
}

.header h1 {
  font-size: 24px;
  margin: 0;
  color: #333;
}

/* TURNTABLE */
.turntable {
  position: relative;
  width: 260px;
  height: 260px;
  margin: 10px 0 20px;
}

/* VINYL */
.vinyl {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 240px;
  height: 240px;
  border-radius: 50%;
  background: repeating-radial-gradient(circle, #111 0%, #000 2%, #111 4%);
  box-shadow: inset 0 0 20px #000;
  border: 3px dotted #444;
  z-index: 1;
}

.vinyl.spin {
  animation: spin 6s linear infinite;
}

/* VINYL CENTER HOLE */
.vinyl-center-hole {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100px;
  height: 100px;
  background: #f4f2f2;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
  box-shadow: inset 0 0 5px #333;
}

/* VINYL LIGHT */
.vinyl-light {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  background: limegreen;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  z-index: 4;
}

.vinyl-light.active {
  opacity: 1;
  animation: light-orbit 3s linear infinite;
}

@keyframes light-orbit {
  from {
    transform: translate(-50%, -50%) rotate(0deg) translateX(70px);
  }
  to {
    transform: translate(-50%, -50%) rotate(360deg) translateX(70px);
  }
}

/* RECORD */
.record {
  position: absolute;
  top: 80px;
  left: 80px;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
  z-index: 2;
  box-shadow: 0 0 10px rgba(0,0,0,0.4);
}

.album-cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* TONEARM */
.tonearm {
  position: absolute;
  top: 5px;
  right: 15px;
  z-index: 5;
  transform: rotate(80deg);
  transform-origin: 20px 20px;
  transition: transform 1s ease;
}

.tonearm.active {
  transform: rotate(120deg);
}

.tonearm-base {
  position: absolute;
  width: 40px;
  height: 40px;
  background: radial-gradient(circle at top left, #444, #222);
  border-radius: 50%;
  box-shadow: 
    inset -2px -2px 4px #000, 
    inset 2px 2px 4px #666;
}

.tonearm-body {
  position:absolute;
  top: 18px;
  left: 18px;
  width: 100px;
  height: 6px;
  background: linear-gradient(80deg, #555, #777, #555);
  border-radius: 3px;
  transform-origin: 0 25%;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.4);
}

.tonearm-head {
  position: absolute;
  top: 15px;
  left: 115px;
  width: 20px;
  height: 12px;
  background: linear-gradient(135deg, #333, #555);
  border-radius: 6px 2px 2px 6px;
  box-shadow: 
    1px 1px 5px rgba(0, 0, 0, 0.4),
    inset -1px 0 3px rgba(0, 0, 0, 0.3);
}

.tonearm-head::after {
  content: '';
  position: absolute;
  width: 3px;
  height: 6px;
  background: #4CAF50;
  border-radius: 1px;
  top: 3px;
  left: 14px;
  box-shadow: 0 0 6px #4CAF50;
}

/* TRACK INFO */
.track-info {
  text-align: center;
  margin: 15px 0;
  width: 100%;
}

.track-info h3 {
  font-size: 16px;
  margin-bottom: 4px;
  color: #333;
}

.track-info p {
  font-size: 13px;
  color: #444;
  margin-bottom: 10px;
}

.progress {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0 10px;
}

.progress input[type="range"] {
  width: 60%;
  height: 4px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 2px;
  outline: none;
  cursor: pointer;
}

.progress span {
  font-size: 12px;
  color: #666;
  min-width: 35px;
}

/* CONTROLS */
.controls {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 0 10px;
  margin-top: 10px;
}

.controls button {
  width: 45px;
  height: 45px;
  color: white;
  border: none;
  border-radius: 50%;
  font-size: 18px;
  cursor: pointer;
  box-shadow: 
    inset 2px 2px 4px rgba(0, 0, 0, 0.3), 
    inset -2px -2px 4px rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.controls button:hover {
  transform: scale(1.1);
}

/* THEME БОЙЫНША БАРЛЫҚ БАТЫРМАЛАРДЫҢ ТҮСІ ӨЗГЕРУІ */
.green-theme .controls button {
  background: radial-gradient(circle at top left, #90EE90, #7CFC00);
}

.purple-theme .controls button {
  background: radial-gradient(circle at top left, #8A2BE2, #9370DB);
  box-shadow: 
    inset 2px 2px 4px rgba(0, 0, 0, 0.3), 
    inset -2px -2px 4px rgba(255, 255, 255, 0.2),
    0 0 10px #8A2BE2;
}

.neon-theme .controls button {
  background: radial-gradient(circle at top left, #39FF14, #32CD32);
  box-shadow: 
    inset 2px 2px 4px rgba(0, 0, 0, 0.3), 
    inset -2px -2px 4px rgba(255, 255, 255, 0.2),
    0 0 10px #39FF14;
}

/* PLAY БАТЫРМАСЫ */
.controls .play {
  transform: scale(1.1);
}

.controls .play:hover {
  transform: scale(1.2);
}

/* ANIMATION */
@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

/* THEME SPECIFIC STYLES */
.green-theme .vinyl-center-hole {
  background: #90EE90;
  box-shadow: inset 0 0 5px #90EE90;
}

.purple-theme .vinyl-center-hole {
  background: #8A2BE2;
  box-shadow: inset 0 0 5px #8A2BE2, 0 0 10px #8A2BE2;
}

.neon-theme .vinyl-center-hole {
  background: #39FF14;
  box-shadow: inset 0 0 5px #39FF14, 0 0 10px #39FF14;
}

.green-theme .vinyl-light {
  background: #90EE90;
}

.purple-theme .vinyl-light {
  background: #8A2BE2;
  box-shadow: 0 0 8px #8A2BE2;
}

.neon-theme .vinyl-light {
  background: #39FF14;
  box-shadow: 0 0 8px #39FF14;
}

.green-theme .tonearm-head::after {
  background: #90EE90;
  box-shadow: 0 0 6px #90EE90;
}

.purple-theme .tonearm-head::after {
  background: #8A2BE2;
  box-shadow: 0 0 6px #8A2BE2;
}

.neon-theme .tonearm-head::after {
  background: #39FF14;
  box-shadow: 0 0 6px #39FF14;
}

/* RESPONSIVE */
@media (max-width: 400px) {
  .player {
    width: 320px;
    padding: 15px;
  }
  
  .turntable {
    width: 240px;
    height: 240px;
  }
  
  .vinyl {
    width: 240px;
    height: 240px;
  }
  
  .record {
    width: 160px;
    height: 160px;
    top: 40px;
    left: 40px;
  }
  
  .theme-buttons {
    top: 12px;
    right: 12px;
  }
  
  .theme-buttons button {
    padding: 6px 12px;
    font-size: 9px;
  }
}